// "1.            .
// 2.  :
// - Modal -      
// - TaskForm -     
// 3.  Modal  TaskForm"

import { Modal } from '../Modal/Modal';
import { TaskForm } from '../TaskForm/TaskForm';

export const TaskModal = ({ onCloseModal, ...rest }) => {
  return (
    <Modal onCloseModal={onCloseModal}>
      <TaskForm onCloseModal={onCloseModal} {...rest} />
    </Modal>
  );
};
